import React, { useEffect, useState } from 'react'
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import styles from './profilePage'
import classNames from "classnames";
import { IconButton, InputAdornment, makeStyles, TextField } from '@material-ui/core';
import GridContainer from './assets/Grid/GridContainer';
import GridItem from './assets/Grid/GridItem'
import Dropzone from 'react-dropzone';
import { Card, Modal, ModalBody, ModalFooter } from "react-bootstrap";
import axios from 'axios';
import { toast } from 'react-toastify';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { styled } from '@mui/material/styles';
// import { DateFormat } from '../common/Format';
// import { bodyBackground } from '../../../util';
import {useTranslation} from "react-i18next";
import profilePic from './assets/Grid/2103 087.jpg';
const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorSuccess}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? 'green' : '#308fe8',
  },
}));
const useStyles = makeStyles(styles);

const buttons = [
  <Button className='bg-success' key="one"><i class="fa fa-heart text-light" aria-hidden="true"></i></Button>,
  <Button className='bg-warning'key="two"><i class="fa fa-thumbs-up text-light" aria-hidden="true"></i></Button>,
  <Button className='bg-primary'key="three"><p className='text-light'>300+</p></Button>,
  <Button className='bg-secondary'key="three"><p className='text-light'>20+</p></Button>,
];
const buttons2 = [
  <Button className='bg-success' key="one"><i class="fa fa-heart text-light" aria-hidden="true"></i></Button>,
 
];
export default function Profile() {
  const classes = useStyles();
  const [selectedImageForSearch, setSelectedForSearch] = React.useState({ file: '', imagePreviewUrl: '' });
  const [userData, setUserData] = useState({
    name: '',
    mail: '',
  });
  const [validMail, setValidE_Mail] = useState('')
  const [profileData, setProfileData] = useState({ userName: '', imageType: '', personImage: '', lastLogin: '' })
  const [sessionToken, setSessionToken] = useState('')
  const [passwordData, setPasswordData] = useState({
    newPassword: '',
    confirmPassword: '',

  });
 
  const [passwordVisibility, setShowPassword] = useState({ showNewPassword: false, showConfirmPassword: false })
  useEffect(() => {
    let token = localStorage.getItem('token')
    setSessionToken(token)
    getProfileData(token)
    // changeBodyBackground()
  }, [])


  const getProfileData = (token) => {

    axios({
      method: "get",
      url: process.env.REACT_APP_API_URL + '/users/getUser',
      headers: { "Authorization": "Bearer " + token }
    }).then(response => {
      console.log("ProfileData", response.data.data.currentLogin)
      setProfileData({
        userName: response.data.data.userName, imageType: response.data.data.imageType,
        personImage: response.data.data.userImg, lastLogin: response.data.data.currentLogin
      })
    })

  }

  const [userModal, setShowUserModal] = useState(false);
  const [passwordModal, setShowPasswordModal] = useState(false);
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");


  const handleClose = () => {
    setShowUserModal(false)
    setShowPasswordModal(false)
    clearValues()
  };
  const clearValues = () => {
    setUserData({ name: '', mail: '', })
    setSelectedForSearch({ file: '', imagePreviewUrl: '' })
    setValidE_Mail("")
  }
  const handleShowUserModal = () => setShowUserModal(true);
  const handleShowPasswordModal = () => setShowPasswordModal(true);
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );
  const onDropSearch = (files) => {
    // setEnableEnroll(false)
    let reader = new FileReader();
    let file = files[0];
    reader.onloadend = () => {
      setSelectedForSearch({
        file: file,
        imagePreviewUrl: reader.result
      });
    }

    reader.readAsDataURL(file)

  };
  const saveUserImage = () => {
    const formData = new FormData();
    formData.append('file', selectedImageForSearch.file)
    axios({
      method: "put",
      url: process.env.REACT_APP_API_URL + '/users/updateImageUser',
      data: formData,
      headers: { "Content-Type": "multipart/form-data", "Authorization": "Bearer " + sessionToken },
    }).then(response => {
      if (response.data.status) {
        toast.success("Profile Picture Updated Successfully !")
        getProfileData(sessionToken)
        window.location.reload()
        setSelectedForSearch({ imagePreviewUrl: '' })
        setTimeout(function () { handleClose() }, 2000);
      } else {
        toast.success("Something Went Wrong")
      }
    }

    )

  }
  const saveUserDetails = (token) => {
    let mailFormat = "^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+$";
    let e_mail = false;
    if (userData.mail !== '') {
      if (userData.mail.match(mailFormat)) {
        e_mail = true;
        setValidE_Mail("");
      } else {
        setValidE_Mail("E-Mail is Not Valid !");
      }
    }


    let updateUserBean = {
      userName: userData.name,
      loginId: userData.mail
    }


    if (e_mail || userData.mail === '') {
      axios.put(process.env.REACT_APP_API_URL + '/users/updateUser', updateUserBean, {
        headers: { "Content-Type": "application/json", "Authorization": "Bearer " + token },
      }).then(response => {
        if (response.data.status === true) {

          if (e_mail) {
            toast.success("Details Updated Successfully")
            setTimeout(function () { toast.warning("Session Expired !") }, 1000);
            // cookie.save('status', "undefined");
            setTimeout(function () { window.history.pushState({}, '', '/') }, 2000);
            setTimeout(function () { window.location.reload() }, 2000);

          } else {
            toast.success("Details Updated Successfully")
            getProfileData(sessionToken)
            setTimeout(function () { handleClose() }, 2000);
          }
        }
      })
    }
  }

  const handleChange = (prop) => (event) => {
    console.log(prop)
    setUserData({ ...userData, [prop]: event.target.value })
    setPasswordData({ ...passwordData, [prop]: event.target.value })
  };
  const handleClickShowPassword = (prop) => (event) => {
    console.log(prop)
    if (prop === 'showConfirmPassword') {
      if (passwordVisibility.showConfirmPassword) {

        setShowPassword({ ...passwordVisibility, showConfirmPassword: false })
      } else {

        setShowPassword({ ...passwordVisibility, showConfirmPassword: true })

      }
    } else {
      if (passwordVisibility.showNewPassword) {

        setShowPassword({ ...passwordVisibility, showNewPassword: false })
      } else {

        setShowPassword({ ...passwordVisibility, showNewPassword: true })

      }
    }

  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const resetPassword = () => {

    let passwordOne = false;
    let passwordTwo = false;
    let passwordMatched = false;

    if (passwordData.newPassword === passwordData.confirmPassword) {
      passwordMatched = true;
    }
    if (passwordData.newPassword.length <= 0) {
      setNewPassword("New Password Required !");
    } else if (
      passwordData.newPassword.length > 1 &&
      passwordData.newPassword.length <= 7
    ) {
      setNewPassword("Min 8 Character is Required !");
    } else {
      passwordOne = true;
      setNewPassword("");
    }


    if (passwordData.confirmPassword.length <= 0) {
      setConfirmPassword("Confirm Password Required !");
    } else if (
      passwordData.confirmPassword.length > 1 &&
      passwordData.confirmPassword.length <= 7
    ) {
      setConfirmPassword("Min 8 Character is Required !");
    } else {
      passwordTwo = true;
      setConfirmPassword("");
    }

    if (passwordOne && passwordTwo && passwordMatched) {
      let changePassword = {
        password: passwordData.confirmPassword
      }
      axios.post(process.env.REACT_APP_API_URL + "/users/changepassword", changePassword).then(response => {
        console.log(response.data.status)
        if (response.data.status) {
          toast.success("Password Successfully Changed");
          handleClose()

        } else {
          toast.error("Something went wrong")
        }
      })

    } else if (passwordData.confirmPassword.length > 1 && passwordData.newPassword.length > 1 && !passwordMatched) {
      toast.error("Password Not Matching")
    }
  }
  const {t} = useTranslation()
  // const changeBodyBackground = () => document.body.style.backgroundColor = bodyBackground()
  let theme = localStorage.getItem('theme')
  return (
    <div className={theme === 'dark' ? 'dark-mode' : 'light-mode'}>
      <div className=' d-flex justify-content-center'>
        <Card className='d-flex rounded-corners profile-details' style={{ width: '40rem' }}>
          <div className='d-flex justify-content-between'>
            
          <Box
          className='mt-5'
      sx={{
        display: 'flex',
        '& > *': {
          m: 1,
        },
      }}
    >
      <ButtonGroup
        orientation="vertical"
        aria-label="vertical outlined button group"
      >
        {buttons}
      </ButtonGroup>
      </Box>
          <Dropzone onDrop={acceptedFiles => onDropSearch(acceptedFiles)}>
            {({ getRootProps, getInputProps }) => (
              <section className=" mt-2">
                <div {...getRootProps({ className: 'imgDropZone' })}>
                  <input {...getInputProps()} />
                  {selectedImageForSearch.imagePreviewUrl !== '' ?
                    <img className={imageClasses} src={selectedImageForSearch.imagePreviewUrl} alt='' style={{ width: '223px', height: '223px' }} /> :
                    <img className={imageClasses} src={profilePic} alt="..." style={{ width: '223px', height: '223px' }} />}
                </div>
                <aside>
                  <ul></ul>
                </aside>
              </section>
            )}
          </Dropzone>
          
          <Box
          className='mt-5'
      sx={{
        display: 'flex',
        '& > *': {
          m: 1,
        },
      }}
    >
      <ButtonGroup
        orientation="vertical"
        aria-label="vertical outlined button group"
      >
        {buttons2}
      </ButtonGroup>
      </Box>
          </div>
          {selectedImageForSearch.imagePreviewUrl !== '' ?
            <div className='d-flex justify-content-center mt-3'><h6 type='button' className='text-primary d-inline' onClick={() => { saveUserImage() }}>Save</h6>
              <h6 type='button' className='text-secondary d-inline ml-3' onClick={() => setSelectedForSearch({ imagePreviewUrl: '' })}>Cancel</h6></div>
            : ""}
          <Card.Body >
            {/* <Card.Title><h3>User Details</h3></Card.Title> */}
            <Card.Text>
              <div className="text-center">
                <h6 className='d-inline text-center '>ANGUNATHAN K</h6>
                <i class="fa fa-pencil-square-o ml-3 d-inline justify-content-end edit-profile" title="Edit Profile" onClick={handleShowUserModal} aria-hidden="true" ></i>
                 <BorderLinearProgress variant="determinate" value={50} />
                
              </div>
              <div className='row'>
              <div className='col-12 text-left'style={{fontWeight:'bold'}}>Basic Information <i class="fa fa-pencil-square-o pull-right mt-2"/></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Location:<p style={{fontWeight:'lighter'}}>Sivagangai,Tamilnadu</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Mobile:<p style={{fontWeight:'lighter'}}>+91 9994139531</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Gender:<p style={{fontWeight:'lighter'}}>Male</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Birthday:<p style={{fontWeight:'lighter'}}>16/07/1998</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Status:<p style={{fontWeight:'lighter'}}>Single</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Age:<p style={{fontWeight:'lighter'}}>23</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Looking For:<p style={{fontWeight:'lighter'}}>Women</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Email:<p style={{fontWeight:'lighter'}}>contactme@gmail.com</p></div>
                <div className='col-12 text-left'style={{fontWeight:'bold'}}>Physical Apperance<i class="fa fa-pencil-square-o pull-right mt-2"/></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Height:<p style={{fontWeight:'lighter'}}>5.6</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Hair Color:<p style={{fontWeight:'lighter'}}>Black</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Weight:<p style={{fontWeight:'lighter'}}>56</p></div>
                <div className='col-6 text-left'style={{fontWeight:'bold'}}>Eye Color:<p style={{fontWeight:'lighter'}}>Brown</p></div>
                <div className='col-12 text-left'style={{fontWeight:'bold'}}>About Me<i class="fa fa-pencil-square-o pull-right mt-2"/></div>
                <div className='col-12 text-left'>Software Developer Currently Lookin Job Change</div>
                <div className='col-6 text-left mt-2'style={{fontWeight:'bold'}}>Current Level: <span className='bg-danger text-light rounded'>12</span></div>
                <div className='col-6 text-left mt-2'style={{fontWeight:'bold'}}>Current Level: <span className='bg-success text-light rounded'>5</span></div>
               
              </div>
              
           
              
            </Card.Text>
          </Card.Body>
        </Card>
      </div>
      <Modal
        // style={{ marginLeft: '6rem' }}
        show={userModal}
        onHide={handleClose}
        backdrop="static"
        className={theme === 'dark' ? 'dark-mode' : 'light-mode'}
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-sm">
            {t('editProfileLabel')}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>

          <form className='Model-Form d-flex justify-content-center' noValidate autoComplete="off">
            <div>
              <TextField id="outlined-basic" value={userData.name}
                onChange={handleChange('name')} InputProps={{
                  startAdornment: (<InputAdornment position="start">
                    <i class="fa fa-user" aria-hidden="true"></i>
                  </InputAdornment>
                  ),
                }} type='text' label={t('fullNameLabel')} variant="outlined" />
            </div>
          </form>
          <form className='Model-Form d-flex justify-content-center' autoComplete="off">
            <div>
              <TextField id="outlined-basic" value={userData.mail}
                onChange={handleChange('mail')} InputProps={{
                  startAdornment: (<InputAdornment position="start">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </InputAdornment>
                  ),
                }} type='email' label={t('e-mailLabel')} variant="outlined" />
            </div>
          </form>
          <span className='ml-5 text-danger'> {validMail}</span>
        </Modal.Body>
        <Modal.Footer>
          <button className="theme-btn style-one" onClick={() => saveUserDetails(sessionToken)}>Save</button>
        </Modal.Footer>
      </Modal>




      <Modal
        show={passwordModal}
        onHide={handleClose}
        backdrop="static"
        className={theme === 'dark' ? 'dark-mode' : 'light-mode'}
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-sm">
            {t('changePasswordOption')}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form className='Model-Form d-flex justify-content-center' noValidate autoComplete="off">
            <div>
              <TextField id="outlined-basic"
                value={passwordData.newPassword}
                onChange={handleChange('newPassword')}
                InputProps={{
                  startAdornment: (<InputAdornment position="start">
                    <i class="fa fa-lock" aria-hidden="true"></i>
                  </InputAdornment>
                  ), endAdornment:
                    (<InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword('showNewPassword')}
                        onMouseDown={handleMouseDownPassword}
                        edge="end"
                      >
                        {passwordVisibility.showNewPassword ? <i class="fa fa-eye" aria-hidden="true"></i> : <i class="fa fa-eye-slash" aria-hidden="true"></i>}
                      </IconButton>
                    </InputAdornment>)
                }}
                type={passwordVisibility.showNewPassword ? "text" : 'password'}
                label={t('newPasswordLabel')} variant="outlined"
                helperText={newPassword}
              />
            </div>

          </form>
          <form className='Model-Form d-flex justify-content-center' noValidate autoComplete="off">
            <div>
              <TextField id="outlined-basic"
                value={passwordData.confirmPassword}
                onChange={handleChange('confirmPassword')}
                InputProps={{
                  startAdornment: (<InputAdornment position="start">
                    <i class="fa fa-lock" aria-hidden="true"></i>
                  </InputAdornment>
                  ), endAdornment:
                    (<InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword('showConfirmPassword')}
                        onMouseDown={handleMouseDownPassword}
                        edge="end"
                      >
                        {passwordVisibility.showConfirmPassword ? <i class="fa fa-eye" aria-hidden="true"></i> : <i class="fa fa-eye-slash" aria-hidden="true"></i>}
                      </IconButton>
                    </InputAdornment>)
                }}
                type={passwordVisibility.showConfirmPassword ? "text" : 'password'}
                label={t('confirmPasswordLabel')}
                variant="outlined"
                helperText={confirmPassword}
              />
            </div>

          </form>

        </Modal.Body>
        <Modal.Footer>
          <button className="theme-btn style-one" onClick={() => { resetPassword() }}>Reset</button>
        </Modal.Footer>
      </Modal>
    </div>
  )
}